.contacts {
  margin-bottom: 120px;
  &__content {
    @include page-grid;
  }
  &__title {
    margin-bottom: 48px;
    grid-column: 1/-1;

    @include on-tablet {
      margin-bottom: 56px;
      grid-column: 1/4;
    }

    @include on-desktop {
      grid-column: span 6;
    }
  }
  &__form {
    display: grid;
    grid-column: span 2;
    row-gap: 16px;
    padding-bottom: 48px;

    @include on-tablet {
      grid-column: span 6;
      padding-bottom: 80px;
    }

    @include on-desktop {
      grid-column: span 6;
      max-width: 411px;
    }
    &__field,
    &__message {
      border: 2px solid #f8f8fa;
      border-radius: 8px;
      box-sizing: border-box;
      font-family: $text-font-family;
      font-weight: 400;
      height: 40px;
      background-color: #f8f8fa;
      resize: none;
      font-size: 14px;
      line-height: 19.6px;
      padding: 16px 14px;
      color: #7e7e83;
      transition: 0.3s ease-in-out;
    }

    &__message {
      height: 148px;
    }
    &__field:hover {
      color: $main-text-color;
      border: 2px solid #d5d7de;
    }
    &__field:active {
      border: 2px solid #292929;
    }

    &__message:hover {
      color: $main-text-color;
      border: 2px solid #d5d7de;
    }
    &__message:active {
      border: 2px solid #292929;
    }
  }

  &__info {
    @include on-desktop {
      grid-column: span 6;
    }
  }
  &__subtitle {
    font-family: $title-font-family;
    font-weight: 400;
    color: #9393a3;
    size: 14px;
    line-height: 19px;
  }
  &__link {
    font-family: $text-font-family;
    font-weight: 400;
    color: #191919;
    font-size: 16px;
    line-height: 22.4px;
    text-decoration: none;
    transition: 0.3s ease-in-out;

    &:hover {
      color: #9393a3;
    }
  }
}
